import React, { Component } from "react";
import Image from "../Image/Image";
import ErrorBoundary from "./../ErrorBoundary";
import {connect} from 'react-redux';
import { goBack, showSearch } from './../../actions/actions';

class Content extends Component {
  constructor(props) {
    super(props);

    this.state = {
      title: "",
      content: [],
      pageNum: 1,
      totalPages: null,
      showSearch: false,
      searchQuery: "",
      showTitle: true,
    };
  }

  componentDidMount() {
    this.loadContent();
    this.scrollListener = window.addEventListener("scroll", (e) => {
      this.loadMore(e);
    });
  }

  loadMore = () => {
    if (this.state.pageNum <= this.state.totalPages) {
      this.setState(
        (prevState) => ({
          pageNum: prevState.pageNum + 1,
        }),
        this.loadContent
      );
    }
  };

  loadContent = () => {
    let { content, pageNum } = this.state;

  
    fetch(`${process.env.PUBLIC_URL}/CONTENTLISTINGPAGE-PAGE${pageNum}.json`, {
        headers : { 
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Headers': 'Authorization'
         }
      
    })
      .then((response) => response.json())
      .then((json) =>
        this.setState({
          title: json.page.title,
          content: [...content, ...json.page["content-items"].content],
          totalPages: json.page["page-num-requested"],
        })
      );
  };

  searchHandler = (e) => {
    this.setState({
      [e.target.name]: e.target.value,
      searchQuery: e.target.value,
    });
    console.log("Searching... " + this.props.searchQuery);
  };

  goBackHandler = () => {
    this.setState({
      showSearch: false,
      showTitle: true,
      searchQuery:""
    });
  };

  showSearch = () => {
    this.setState({
      showSearch: !this.state.showSearch,
      showTitle: !this.state.showTitle,
    });
  };

  render() {
    return (
      <React.Fragment>
        <div className="fixed w-full">
          <div className="flex w-full justify-around">
            <div
              className=" w-5 h-5 ml-3 mt-5 z-20 cursor-pointer"
              onClick={this.goBackHandler}
            >
              <img src={require("../../slices/Back.png")} className=" h-4" />
            </div>
            <div className="w-70 sm:text-left lg:text-center mt-4 ml-5 z-20">
              <h3>{this.state.showTitle ? this.state.title : null}</h3>
            </div>
            {this.state.showSearch ? (
              <input
                type="text"
                name="searchQuery"
                onChange={this.searchHandler}
                className=" w-full lg:h-8 mr-3  rounded color-black mt-3 z-20 pl-2 outline-none"
              />
            ) : null}
            {!this.state.showSearch ? (
              <div
                className="w-5 float-right ml-5 mt-5 mr-4 z-20 cursor-pointer"
                onClick={this.showSearch}
              >
                <img
                  src={require("../../slices/search.png")}
                  className=" h-4"
                />
              </div>
            ) : (
              "X"
            )}
          </div>
          <img
            src={require("../../slices/nav_bar.png")}
            className="absolute top-0 w-full h-24"
          />
        </div>

        <div className="container mx-auto">
          <div className="grid grid-cols-3 gap-2 pr-2 pl-2">
            {this.state.content
              .filter((text) =>
                text.name.toLowerCase().includes(this.state.searchQuery)
              )
              .map((image, index) => (
                <ErrorBoundary key={index}>
                    <Image source={image["poster-image"]} name={image.name} />
                </ErrorBoundary>  
              ))}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

const mapStateToProps=(state)=>{
  return{
    showSearch: state.showSearch,
    searchQuery: state.searchQuery,
    showTitle: state.showTitle,
  }
}

const mapDispatchToProps=(dispatch)=>{
  return{
    goBack: ()=>dispatch(goBack()),
    showSearchBar: ()=>dispatch(showSearch())
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(Content);
