const initialState ={
    showSearch: false,
    searchQuery: "",
    showTitle: true,
}

function reducer(state=initialState,action) {
   switch(action.type){
       case 'GO_BACK':
           console.log(state)
           return{
               ...state,
               showSearch: false,
               searchQuery:"",
               showTitle:true
           };

           case 'SHOW_SEARCH':
            console.log(state)
            return{
                ...state,
                showSearch: !state.showSearch,
                showTitle: !state.showTitle
            };

        default: 
            return state;
   }
}

export default reducer
